.product__block {
    display: block;
    width: 280px;
    border: 1px solid rgb(221, 219, 219);
    background-color: #fff;
    border-radius: 8px;
    position: relative;
    &:hover {
        box-shadow: 3px 4px 38px -7px rgba(0, 0, 0, 0.75);
        -webkit-box-shadow: 3px 4px 38px -7px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 3px 4px 38px -7px rgba(0, 0, 0, 0.75);
    }
    & > img {
        margin: 25px auto;
    }
    &--span {
        background-color: var(--greenMain);
        border-top-right-radius: 10px;
        padding: 8px;
        font-size: 16px;
        color: #fff;
        position: absolute;
        top: -30px;
    }
    &--text {
        background-color: #efefef;
        color: green;
        position: relative;
        & > p {
            font-size: 18px;
            padding: 15px;
        }
        & > div {
            display: flex;
            & > span {
                background-color: #cdcccc;
                border-top-left-radius: 10px;
                display: block;
                margin-left: auto;
                width: 100px;
                padding: 10px;
                font-size: 20px;
                color: #000;
            }
        }
    }
}

.product__container {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    padding: 50px;
}

.product__block--btn {
    background-color: green;
    color: #fff;
    width: 40px;
    height: 40px;
    font-size: 25px;
    padding-top: 5px;
    border-bottom-right-radius: 8px;
}

.block__filter {
    display: flex;
    width: 1000px;
    margin-left: auto;
    margin-right: auto;
    & > input {
        border-radius: 20px;
        border: 1px solid gray;
        padding: 10px;
        font-size: 18px;
    }
}

.line {
    text-align: center;
    font-weight: 900;
    margin-top: 20px;
    width: 80%;
    height: 1px;
    background-color: #000;
}
